.receipt {

	padding-bottom: rem(80);

	@include min {
		padding-bottom: rem(40);
	}

	&__header {
		max-width: rem(646);
		margin-bottom: rem(70);

		@include min768 {
			max-width: 100%;
			margin-bottom: rem(32);
		}
	}

	.flex {

		.column_left {
			flex: 0 0 rem(535);
			max-width: rem(535);
			margin-right: rem(20);

			@include min1050 {
				flex: 0 0 60%;
				margin-right: 0;
				padding-right: rem(20);
			}

			@include min768 {
				flex: 0 0 100%;
				padding-right: 0;

				order: 2;
			}
		}	

		.column_right {
			
			flex: 0 0 rem(520);
			max-width: rem(520);

			@include min1050 {
				flex: 0 0 40%;
				max-width: 40%;
			}

			@include min768 {
				order: 1;
				flex: 0 0 100%;
				max-width: 100%;

				margin-bottom: rem(24);
			}
		}
	}
}

.media_block {
	border-radius: rem(16);
	background-color: var(--pale-grey);
	padding: rem(37) rem(37) rem(32) rem(37);

	display: flex;
	flex-direction: column;
	align-items: center;

	@include min {
		padding: rem(24) rem(24) rem(19) rem(24);
	}

	&__content {
		border-radius: rem(4);
		height: rem(250);
		width: 100%;
		overflow: hidden;
	}

	p {
		margin-top: rem(32);
	}

	// iframe {

