.what_is_an_invoice {
	padding-bottom: rem(80);

	@include min {
		padding-bottom: rem(40);
	}

	// .container {
		
	// }

	.nav_tabs {
		margin-bottom: rem(64);

		@include min {
			margin-bottom: rem(34);
		}
	}

	.flex {
		margin-right: rem(-26);

		@include w_default {
			margin-right: rem(-70);
		}
	}

	.column_left {
		flex: 0 0 rem(540);
		max-width: rem(540);

		margin-right: rem(90);

		@include min1050 {
			flex: 0 0 60%;
			max-width: 60%;
			margin-right: 0;
			padding-right: rem(30);
		}

		@include min768 {
			flex: 0 0 100%;
			max-width: 100%;
			padding-right: 0;
		}

		h2 {
			margin-bottom: rem(24);
		}

		p {
			margin-bottom: rem(32);
			font-size: rem(20);
			line-height: rem(28);
			letter-spacing: -0.14px;

			max-width: rem(531);

			@include min768 {
				font-size: rem(16);
				max-width: 100%;
				margin-bottom: rem(24);
			}
		}
	}

	.column_right {
		// position: relative;
		flex: 0 0 rem(450);
		// width: 100%;
		max-width: rem(450);

		@include w_default {
			flex: 0 0 rem(520);
			max-width: rem(520);
		}

		@include min1050 {
			flex: 0 0 40%;
			max-width: 40%;
		}

		@include min768 {
			display: none;
		}

		img {
			margin-top: rem(170);
			// position: absolute;
			// top: 0;
			// right: 0;
			// width: rem(520);
		}
	}
}