.footer {
	background-color: #f5f0f4;
	padding-top: rem(92);
	padding-bottom: rem(83);

	@include min {
		padding-top: rem(34);
		padding-bottom: rem(40);
	}

	&__top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-right: rem(-80);
		margin-bottom: rem(80);

		@include min768 {
			margin-right: 0;
			margin-bottom: rem(24);
		}

		&-left {
			@include min768 {
				width: 100%;
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				margin-bottom: rem(24);

			}
			.logo {
				margin-bottom: rem(32);
			}

		}

		&-right {
			display: flex;
			flex-wrap: wrap;

			@include min768 {
				width: 100%;
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-bottom: rem(73);

		@include min768 {
			margin-bottom: rem(40);
		}

		&-left {
			@include min768 {
				flex: 0 0 100%;
				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				margin-bottom: rem(20);
			}
			
			a {
				margin-right: rem(8);

				&:last-child {
					margin-right: 0;
				}
			}
		}

		&-right {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include min768 {
				flex: 0 0 100%;
				// justify-content: center;
				flex-direction: column;
			}

			p {
				font-size: rem(14);
				font-weight: var(--font-w-sm);

				@include min768 {
					margin-bottom: rem(12);
				}
			}
			
			.social {
				margin-left: rem(16);

				@include min768 {
					margin-left: 0;
				}
			}
		}
	}

	&__copy {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		@include min768 {
			justify-content: center;
		}

		a, p {
			font-size: rem(14);
		}

		a {
			position: relative;
			color: var(--cornflower);

			margin-right: rem(6);

			@include min768 {
				&:last-of-type {
					margin-right: 0;
				}
			}

			&:hover {
				color: var(--dark-indigo);
			}

			&::after {
				content: "-";
				font-size: rem(14);
				color: var(--slate);
				margin-left: rem(6);

				pointer-events: none;
			}

			&:last-of-type {
				&::after {
					display: none;
				}
			}
		}

		p {
			margin-left: rem(24);

			@include min768 {
				margin-top: rem(12);
				margin-left: 0;
				flex: 0 0 100%;
				text-align: center;
			}
		}

	}
}

.social {
	display: flex;
	flex-wrap: wrap;

	a {
		margin-right: rem(16);

		i {
			font-size: rem(24);
		}

		&:last-child {
			margin-right: 0;
		}
	}
}

.nav_col {
	width: rem(200);
	display: flex;
	flex-direction: column;

	@include min {
		width: rem(180);
	}

	@include min768 {
		width: calc((100% - 4rem) / 3);
		margin-right: rem(20);

		margin-bottom: rem(20);

		&:last-child {
			margin-right: 0;
		}
	}

	@include min600 {
		width: 50%;

		margin-right: 0;
	}

	

	&__title {
		font-size: rem(16);
		line-height: rem(30);
		font-weight: var(--font-w-sm);
		color: var(--dark-indigo);
		text-transform: uppercase;

		margin-bottom: rem(34);

		@include min768 {
			margin-bottom: rem(14);
		}
	}

	ul {

		li {

			a {
				line-height: rem(30);
				color: var(--dark-indigo);

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
}