.menu_mobile {
	display: none;

	@include min768 {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: white;
		opacity: 0;
		display: flex;
		flex-direction: column;
		// align-items: flex-end;

		padding: rem(100) rem(20) rem(20) rem(20);

		z-index: 1;

		transition: all 300ms ease;

		overflow-y: auto;

		transform: translateX(100%);

		> ul {
			margin-bottom: rem(40);

			> li {
				margin-bottom: rem(20);

				text-align: left;

				> a, span {
					font-size: rem(22);
					display: flex;
					align-items: center;
					// justify-content: flex-end;
					font-weight: var(--font-w-sm);

					i {
						margin-left: rem(10);
						font-size: rem(8);
					}
				}

				.dropmenu {
					margin-top: rem(20);

					li {
						margin-bottom: rem(6);

						&:last-child {
							margin-bottom: 0;
						}

						a {
							font-size: rem(18);
						}
					}
				}
			}
		}

		&__bottom {
			display: flex;
			flex-direction: column;
			align-items: flex-start;

			.first {
				margin-bottom: rem(14);
				font-size: rem(20);
			}
		}
	}
}

.menu_mobile.active {
	transform: translateX(0%);
	opacity: 1;
}