.comparison {
	padding: rem(80) 0;
	margin-bottom: rem(82);

	@include min {
		padding: rem(40) 0;
		margin-bottom: rem(40);
	}


	.container {
		> h2, h3 {
			margin-bottom: rem(24);
		}

		> p {
			font-size: rem(20);
			line-height: rem(28);
			letter-spacing: -0.14px;
			margin-bottom: rem(48);
		}
	}
	

	.flex {
		.column_left, .column_right {
			flex: 0 0 50%;
			max-width: 50%;
		}

		// @include min1050 {
		// 	.column_left {
		// 		flex: 0 0 35%;
		// 		max-width: 35%;
		// 	}
		// 	.column_right {
		// 		flex: 0 0 65%;
		// 		max-width: 65%;
		// 	}
		// }
		
		@include min1050 {
			.column_left {
				display: none;
			}

			.column_right {
				flex: 0 0 100%;
				max-width: 100%;
			}
		}

		.column_left {
			h2,
			h3 {
				margin-bottom: rem(38);

				span {
					color: var(--cornflower);
					
					&::after {
						content: "";
						display: block;
					}
				}
			}
		}

		.column_right {
			.tile_col_block {
				margin-top: rem(26);
			}
		}
	}
}

.comparison_pricing_page {
	background-color: var(--pale-grey);
	margin-bottom: 0;

	.column_left {
		margin-top: rem(48);

		.tile_col__item {
			background-color: transparent;

			&:nth-child(2n) {
				background-image: linear-gradient(to left, #f5f1f3, var(--pale-grey), #ffffff);
			}
		}
	}
	.column_right {
		.tile_col_block {
			margin-top: 0 !important;
			background-color: white;
		}
	}
}