.about_help {
	margin-top: rem(80);
	margin-bottom: rem(80);

	@include min {
		margin-top: rem(40);
		margin-bottom: rem(40);
	}

	.container {
		position: relative;

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__left, &__right {
		flex: 0 0 rem(520);
		max-width: rem(520);
	}

	@include min1050 {

		&__left {
			flex: 0 0 60%;
			max-width: 60%;
			padding-right: rem(32);
		}

		&__right {
			flex: 0 0 40%;
			max-width: 40%;
		}
		
	}

	@include min768 {
		&__left {
			flex: 0 0 100%;
			max-width: 100%;
			padding-right: 0;
		}

		&__right {
			display: none;
		}
	}

	&__left {
		h2,h3 {
			margin-bottom: rem(24);
		}

		p {
			font-size: rem(20);
			line-height: rem(28);
			letter-spacing: -0.14px;
		}
	}

	&__right {
		position: absolute;
		top: -50%;
		right: 0;

		@include min1050 {
			position: relative;
			top: auto;
		}
	}
}