.header {
	background: #fcfafb url(../img/bg-image.png) no-repeat bottom center;
	background-size: cover;

	padding-top: rem(152);

	@include min768 {
		padding-top: rem(100);
	}

	&__top {
		padding-top: rem(7);
		// margin-bottom: rem(76);

		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 100;
		

		// &::before {
		// 	content: "";
		// 	display: block;
		// 	width: 100%;
		// 	// height: rem(174);
		// }

		@include min {
			margin-bottom: rem(40);
		}

		.container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include min768 {
				justify-content: space-between;
			}
		}

		&-burger {
			display: none;

			position: relative;
			width: rem(30);
			height: rem(18);
			// display: flex;

			margin-left: rem(20);

			@include min768 {
				display: flex;
			}

			.line {
				position: absolute;
				left: 0;
				width: 100%;
				height: rem(2);
				border-radius: rem(2);
				background-color: #0f141a;

				transition: all 300ms ease;

				&:first-child {
					top: 0;
				}

				&:nth-child(2), &:nth-child(3) {
					top: 50%;
					margin-top: rem(-1);
				}

				&:last-child {
					bottom: 0;
				}
			}

		}

		&-burger.active {
			.line {
				&:first-child {
					transform: translateX(rem(-40));
					background-color: transparent;
				}

				&:nth-child(2) {
					transform: rotate(45deg);
					top: rem(8);
				}

				&:nth-child(3) {
					transform: rotate(-45deg);
					top: rem(8);
				}

				&:last-child {
					transform: translateX(rem(40));
					background-color: transparent;
				}
			}
		}

		&-logo {
			margin-right: rem(88);
			margin-top: rem(10);

			@include min {
				margin-right: rem(42);
			}
		}

		&-nav {

			@include min768 {

				display: none;

				// position: fixed;
				// top: rem(72);
				// left: 0;
				// width: 100%;
				// height: 100%;
				// background-color: white;
				// z-index: 100;

				// padding: rem(40) rem(30);

				// // box-shadow: 0 10px 32px -5px #e1a7e8;

				// transition: all 500ms ease;
			}

			> ul {

				display: flex;
				flex-wrap: wrap;
				// align-items: center;

				@include min768 {
					flex-direction: column;
				}

				> li {
					position: relative;
					margin-right: rem(40);

					z-index: 10 !important;

					@include min {
						margin-right: rem(24);
						
					}

					@include min768 {
						margin-right: 0;
						// margin-bottom: rem(24);
					}

					&:last-child {
						margin-right: 0;
					}

					> a {
						color: var(--dark-indigo);
						font-weight: var(--font-w-sm);

						display: flex;
						align-items: center;

						transition: all 200ms ease;

						@include min768 {
							padding-left: rem(20);
							padding-right: rem(20);
						}

						&:hover {
							color: var(--cornflower);
						}

						i {
							pointer-events: none;
							margin-top: rem(2);
							margin-left: rem(16);
							font-size: rem(6);
						}
					}

					.dropmenu {
						position: absolute;
						z-index: 10;
						top: rem(41);
						left: 0;
						padding: rem(20) rem(16);
						border-radius: rem(2);
						background-color: white;
						box-shadow: 0 10px 32px -5px #e1a7e8;

						z-index: 100;

						display: none;

						@include min768 {
							position: relative;
							top: auto;
							// background-color: red;
							width: 100%;
						}

						&::before {
							content: "";
							position: absolute;
							top: rem(-8);
							left: 50%;
							margin-left: rem(-4);
							border: rem(8) solid transparent;
							border-bottom-color: white;
							border-top: 0;
							width: 0;
							height: 0;
							
						}

						li {

							a {
								line-height: rem(30);
								white-space: nowrap;
								transition: all 200ms ease;
								color: var(--slate);

								&:hover {
									color: var(--cornflower);
								}
							}
						}
					}
				}
				> li.active_menu {
					.dropmenu {
						display: block;
					}
				}
			}
		}

		&-right {
			margin-left: auto;
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include wm_default {
				margin-right: rem(-80);
			}

			@include w_default {
				margin-right: rem(-120);
			}

			@include min768 {
				display: none;
			}

			.first {
				margin-right: rem(30);

				font-weight: var(--font-w-sm);
				color: var(--cornflower);

				&:hover {
					color: var(--cornflower-h);
				}
			}
		}
	}

	

	&__bottom {
		overflow: hidden;
		display: grid;
		grid-template-columns: calc((100% - var(--w-container))/2) 6.5fr 3.5fr calc((100% - var(--w-container))/2);

		@include min1050 {
			grid-template-columns: calc((100% - var(--w-container))/2) 5fr 3.5fr calc((100% - var(--w-container))/2);
			margin-left: 20px;
			// margin-right: 20px;
		}

		@include min768 {
			padding-bottom: rem(80);

			display: flex;
		}

		@include min540 {
			// padding: 0 0 rem(80) 0;
		}

		h1 {
			font-size: rem(48);
			font-weight: var(--font-w-sm);
			color: var(--dark-indigo);

			@include min1050 {
				font-size: rem(34);
			}

			@include min768 {
				font-size: rem(30);
			}
		}

		&-left {
			// max-width: rem(682);
			// flex: 0 0 rem(682);

			grid-column: 2 / span 1;
			// grid-column: auto;

			@include min1050 {
				padding-right: rem(40);
			}

			h1, p, .button-default {
				margin-bottom: rem(24);

				@include min768 {
					margin-bottom: rem(14);
				}
			}

			

			.descr {
				font-size: rem(18);
				max-width: rem(590);
			}

			.info {
				font-size: rem(13);

				a {
					color: var(--cornflower);
					text-decoration: underline;

					&:hover {
						text-decoration: none;
					}
				}
			}

			// .button-default {
				
			// }
		}

		&-right {
			position: relative;
			
			grid-column-end: span 2;

			@include min768 {
				display: none;
			}
			

			img {
				&:first-child {
					position: absolute;
					top: 0;	
					left: rem(90);

					width: 100%;
					// height: 100%;
					// object-fit: cover;
					// object-position: top left;
					// z-index: -1;
				}

				&:last-child {
					position: relative;
					top: 0;
					left: rem(-45);
					z-index: 1;
					margin-top: rem(63);

					width: rem(262);
				}
			}

			.static {
				position: relative !important;
				top: auto !important;
				left: auto !important;
				margin-top: 0 !important;
			}
		}
	}

	&__bottom_static {
		display: flex;
		flex-wrap: wrap;

		max-width: var(--w-container);
		width: 100%;
		margin-left: auto;
		margin-right: auto;

		@include min1050 {
			max-width: 100%;
			width: calc(100% - 40px);
		}
	}

	&__bottom-col {
		display: flex;
		overflow: visible;

		padding-bottom: rem(180);

		@include min1050 {
			padding-bottom: rem(80);

			width: calc(100% - 40px);
		}

		.container {
			display: flex;
			width: 100%;
			flex-direction: column;
		}

		&__text {
			margin-bottom: rem(64);

			@include min {
				margin-bottom: rem(32);
			}

			h1 {
				margin-bottom: rem(24);
			}

			p {
				font-size: rem(18);
			}
		}

		&__items {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			.item {
				flex: 0 0 rem(530);
				max-width: rem(530);
				border-radius: rem(8);
				background-color: white;
				box-shadow: 0 3px 12px 0 rgba(0, 0, 0, 0.12),
				0 22px 24px -5px rgba(0, 0, 0, 0.08);

				padding: rem(32);

				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;

				@include min1050 {
					flex: 0 0 calc((100% - 1.25rem) / 2);
					max-width: calc((100% - 1.25rem) / 2);

					padding: rem(14);
				}

				@include min {
					flex: 0 0 100%;
					max-width: 100%;
					padding: rem(32);

					margin-bottom: rem(20);

					&:last-child {
						margin-bottom: 0;
					}
				}

				@include min540 {
					padding: rem(14);
				}

				&__left {
					flex: 0 0 rem(203);
					max-width: rem(203);

					@include min1050 {
						flex: 0 0 45%;
						max-width: 45%;
					}

					b, strong {
						font-size: rem(28);
						font-weight: var(--font-w-sm);
						color: var(--dark-indigo);
						margin-bottom: rem(13);
						display: block;
					}

					p {
						line-height: rem(23);
						letter-spacing: -0.11px;
						margin-bottom: rem(24);
					}

					.button-default {
						width: rem(148);
						display: flex;
						align-items: center;

						i {
							margin-left: auto;
							margin-top: rem(2);
						}
					}
				}

				&__right {
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;

					flex: 0 0 rem(223);
					max-width: rem(223);

					@include min1050 {
						flex: 0 0 45%;
						max-width: 45%;
					}

					b, strong {
						font-size: rem(28);
						font-weight: var(--font-w-sm);
						color: var(--slate);
						margin-bottom: rem(8);
					}

					span {
						color: var(--bluey-grey);
						line-height: rem(23);
						letter-spacing: -0.11px;

						&:first-of-type {
							margin-bottom: rem(8);
						}

						@include min768 {
							text-align: center;
						}
					}
				}
			}
		}
	}

}

.header.header_fixed {
	
	.header__top {
		background-color: white;
	}

// 	&::before {
// 		content: "";
