// 		width: 100%;
// 		height: rem(174);
// 	}
}

.header_static {
	background: white;

	padding-top: rem(80);

	.header__top {
		margin-bottom: 0;
		background-color: white;
		height: rem(80);
		box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);

		.container {
			padding-top: rem(4);
		}
	}

	.header__about {
		height: rem(240);

		.container {

			p {
				max-width: rem(530);
			}
		}
	}

	.header__blog {
		background: url(../img/hero-bg.jpg) no-repeat top center;
		background-size: cover;
		height: rem(220);

		.container {
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;

			h1 {
				font-size: rem(48);
				font-weight: var(--font-w-sm);
				color: white;
				margin-bottom: rem(16);
			}

			p {
				font-size: rem(18);
				color: white;
				opacity: .8;
			}
		}
	}
}

.header_white {
	background-color: white;
}

#header_overview {
	background-color: white;

	.header__bottom {
		grid-template-columns: calc((100% - 1080px)/2) 6fr 4fr calc((100% - 1080px)/2);

		&-right {
			img {
				&:last-child {
					left: rem(-50);
				}
			}
		}
	}
}

.how_to_create_invoice {
	overflow: visible;
	background-color: white;
	justify-content: space-between;

	padding-bottom: rem(147);

	@include min1050 {
		padding-bottom: rem(80);
	}

	@include min768 {
		padding-bottom: rem(30);
	}

	.header__bottom {
		overflow: visible;
	}

	.header__bottom-left {
		flex: 0 0 rem(682);
		max-width: rem(682);

		@include min1050 {
			flex: 0 0 60%;
			max-width: 60%;
		}

		@include min768 {
			flex: 0 0 100%;
			max-width: 100%;
		}
	}

	.header__bottom-right {
		flex: 0 0 rem(357);
		max-width: rem(357);

		@include min1050 {
			flex: 0 0 40%;
			max-width: 40%;
		}

		@include min768 {
			display: none;
		}

		img {
			left: rem(30) !important;

			@include w_default {
				left: rem(80) !important;
			}

			@include min1050 {
				left: 0 !important;
			}
		}
	}
}

