.questions {
	margin-bottom: rem(160);
	margin-top: rem(80);

	@include min {
		margin-top: rem(40);
		margin-bottom: rem(80);
	}

	&__header {
		text-align: center;
		margin-bottom: rem(80);

		@include min {
			margin-bottom: rem(40);
		}
	}
}

.accordion {

	&__item {
		margin-bottom: rem(24);
		padding-bottom: rem(24);
		border-bottom: rem(1) solid #f2e9f0;

		&:last-child {
			margin-bottom: 0;
		}

		i {
			width: rem(16);
			height: rem(16);
			margin-right: rem(16);
		}

		&-icon {
			background: url(../img/plus.svg) no-repeat;
			background-size: 100%;
		}

		&-button {
			display: flex;
			// flex-wrap: wrap;

			i {
				margin-top: rem(4);

				@include min540 {
					margin-top: rem(3);
				}
			}

			b {
				font-size: rem(18);
				color: var(--dark-indigo);
				font-weight: var(--font-w-sm);
				transition: all 200ms ease;
				text-align: left;

				@include min540 {
					font-size: rem(16);
				}

				&:hover {
					color: var(--cornflower);
				}
			}
		}

		&-content {
			padding-top: rem(24);
			// padding-bottom: rem(24);

			display: none;

			p {
				line-height: rem(24);
				letter-spacing: 0.14px;
			}
		}
	}

	&__item.active {

		.accordion__item-icon {
			background: url(../img/minus.svg) no-repeat;
			background-size: 100%;
		}

		.accordion__item-content {
			display: block;
		}
	}
}