.reviews {
	background-color: #fcfafb;
	padding-top: rem(80);
	padding-bottom: rem(108);

	@include min {
		padding-top: rem(40);
		padding-bottom: rem(54);
	}

	@include min768 {
		padding-bottom: rem(34);
	}

	&__header {
		margin-bottom: rem(69);

		max-width: rem(729);
		margin-left: auto;
		margin-right: auto;

		@include min {
			margin-bottom: rem(34);
		}

		@include min768 {
			max-width: 100%;
		}

		img {
			margin-left: auto;
			margin-right: auto;
			margin-bottom: rem(24);

			display: block;
		}

		h2,h3 {
			text-align: center;
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		@include min768 {
			justify-content: center;
		}

		.item {
			width: calc((100% - 3.75rem) / 3);
			margin-right: rem(30);

			border-radius: rem(8);
			box-shadow: 0 5px 16px -4px rgba(0, 0, 0, 0.12);
			background-color: white;

			padding: rem(5);

			@include min768 {
				width: calc((100% - 1.3rem) / 2);
				margin-right: rem(20);

				margin-bottom: rem(20);

				&:nth-child(2n) {
					margin-right: 0;
				}

				&:last-child {
					margin-right: 0 !important;
				}
			}

			@include min540 {
				width: 100%;
				margin-right: 0;
			}

			&:nth-child(3n) {
				margin-right: 0;

				@include min768 {
					margin-right: rem(20);
				}
			}

			> img {
				width: 100%;
				border-radius: rem(8);
				height: rem(150);
				margin-bottom: rem(14);

				object-fit: cover;

				@include min540 {
					margin-bottom: rem(6);
				}
			}

			&__text {
				padding: rem(5) rem(15) rem(15) rem(15);

				img {
					margin-bottom: rem(10);

					
				}

				p {
					letter-spacing: -0.09px;
					margin-bottom: rem(20);
					max-width: rem(290);

					@include min540 {
						max-width: 100%;
					}
				}

				ul {
					li {
						font-size: rem(14);
						font-weight: var(--font-w-sm);
						letter-spacing: -0.09px;
						color: var(--slate);
					}
				}
			}
		}
	}
}