.sample_templates {
	padding-top: rem(80);
	padding-bottom: rem(64);
	margin-bottom: rem(80);

	background-color: #fcfafb;

	@include min {
		padding-top: rem(40);
		padding-bottom: rem(32);
		margin-bottom: rem(40);
	}

	h2 {
		margin-bottom: rem(24);
	}

	p {
		font-size: rem(20);
		line-height: rem(28);
		letter-spacing: -0.14px;
	}

	.flex {
		margin-top: rem(62);

		@include min {
			margin-top: rem(32);
		}

		img {
			overflow: hidden;
			border-radius: rem(6);

			width: rem(200);

			margin-right: rem(20);

			@include min1050 {
				width: calc((100% - 5rem) / 5);
			}

			@include min768 {
				width: calc((100% - 2.5rem) / 3);
				margin-bottom: rem(20);

				&:nth-child(3n) {
					margin-right: 0;
				}
			}

			@include min540 {
				width: calc((100% - 1.25rem) / 2);

				&:nth-child(2n) {
					margin-right: 0;
				}
				&:nth-child(3n) {
					margin-right: rem(20);
				}
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}
}