.partner {
	padding-bottom: rem(76);
	margin-bottom: rem(84);
	background-color: #fcfafb;

	@include min768 {
		padding-bottom: rem(40);
		margin-bottom: rem(44);
	}

	&__header {
		display: flex;
		flex-direction: column;

		h2 {
			margin-bottom: rem(24);
		}

		p {
			margin-bottom: rem(26);
			max-width: rem(531);

			font-size: rem(20);
			line-height: rem(28);
			letter-spacing: -0.14px;
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item {
			width: calc((100% - 2.44rem) / 4);
			margin-right: rem(13);

			height: rem(130);

			@include flex-center;

			@include min768 {
				width: calc((100% - 2.44rem) / 3);

				&:nth-child(3n) {
					margin-right: 0;
				}
			}

			@include min600 {
				width: calc((100% - 2.44rem) / 2);

				&:nth-child(2n) {
					margin-right: 0 !important;
				}
			}

			@include min460 {
				width: 100%;
				height: rem(84);
				margin-right: 0 !important;
			}

			&:nth-child(4n) {
				margin-right: 0;

				@include min768 {
					margin-right: rem(13);
				}
			}
		}
	}
}