.quickly {
	background-color: var(--pale-grey);
	padding: rem(80) 0;

	@include min {
		padding: rem(40) 0;
	}

	.flex {

		.column_left {
			flex: 0 0 rem(436);
			max-width: rem(436);
			margin-right: rem(104);

			@include min1050 {
				flex: 0 0 50%;
				max-width: 50%;
				margin-right: 0;
				padding-right: rem(80);
			}

			@include min {
				padding-right: rem(32);
			}

			@include min768 {
				display: none;
			}
			
		}

		.column_right {
			flex: 0 0 rem(540);
			max-width: rem(540);

			@include min1050 {
				flex: 0 0 50%;
				max-width: 50%;
			}

			@include min768 {
				flex: 0 0 100%;
				max-width: 100%;
			}

			.accordion {
				margin-bottom: rem(89);

				@include min {
					margin-bottom: rem(40);
				}
			}

			.button-default {

			}
		}
	}

	h2, h3 {
		margin-bottom: rem(80);

		@include min {
			margin-bottom: rem(40);
		}
	}
}

.quickly.quickly_pricing_page {
	background-color: white;

	.column_right {
		h2, h3 {
			margin-bottom: rem(24);
		}

		> p {
			&:first-of-type {
				font-size: rem(20);
				line-height: rem(28);
				letter-spacing: -0.14px;
				margin-bottom: rem(24);
			}
			&:last-of-type {
				line-height: rem(24);
				margin-bottom: rem(24);
			}
		}

		> .flex {
			.button-default {
				margin-right: rem(16);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}